import { ClipboardJS } from "../../JsTool/ClipboardJS";
import View from "../../MVCFramework/View";
import Toast from "../../Tools/Toast";
import UploadIMG from "../../Tools/UploadIMG";
import Util from "../../Tools/Util";
import BanKItem from "./PayEn/BankItem";
import BounsItem from "./PayEn/BounsItem";
import UIItemDay from "./PayEn/UIItemDay";


export default class ViewApp extends View 
{
    ContentPanel:cc.Widget;

    selfPlayerId:string;



    private RechargeCom: cc.Node;
    private RechargeInfoCom: cc.Node;
    private RechargeHistoryCom: cc.Node;

    private BankList: cc.Node;
    private BonusList: cc.Node;
    //button
    private BtnUpload: cc.Node;

    //editBox
    private editbox_imput: cc.EditBox;

    //txt_info1
    private txt_info1: cc.Label;
    private txt_info2: cc.Label;
    private txt_info3: cc.Label;
    private txt_info4: cc.Label;
    private txt_info5: cc.Label;
    private txt_info6: cc.Label;
    private txt_info7: cc.Label;

    //prefabe
    private bankItem: cc.Node;
    private BonusItem: cc.Node;
    //Array
    private BankItems: BanKItem[];
    private BonusItems: BounsItem[];

    //date
    private date: Date;
    private year: number;
    private month: number;
    private day: number;
    private pfgListDay: cc.Node[];
    private ndDays: cc.Node;
    private pfbDay: cc.Node;
    private lbYearMonth: cc.Label;


    public SetSelfPlayerID(playerId)
    {
        this.selfPlayerId = playerId;
    }

    public OnAwake()
    {
        this.ContentPanel=this.FindTransform("ContentPanel").getComponent(cc.Widget);
        this.RechargeCom = this.FindTransform("RechargeCom");
        this.RechargeInfoCom = this.FindTransform("RechargeInfoCom");
        this.RechargeHistoryCom = this.FindTransform("RechargeHistoryCom");
        this.editbox_imput = this.FindTransform("editbox_imput").getComponent(cc.EditBox);
        this.bankItem = this.FindTransform("bankItem");
        this.BonusItem = this.FindTransform("Bouns")
        this.BankList = this.FindTransform("BankList").getChildByName("view").getChildByName("content");
        this.BonusList = this.FindTransform("BonusList").getChildByName("view").getChildByName("content");

        this.BtnUpload = this.FindTransform("BtnUpload");
        this.ndDays = this.FindTransform("ndDays");
        this.pfbDay = this.FindTransform("UIItemDay");
        this.lbYearMonth = this.FindTransform("lbYearMonth").getComponent(cc.Label);

        this.txt_info1 = this.FindTransform("txt_info1").getChildByName("text").getComponent(cc.Label);
        this.txt_info2 = this.FindTransform("txt_info2").getChildByName("text").getComponent(cc.Label);
        this.txt_info3 = this.FindTransform("txt_info3").getChildByName("text").getComponent(cc.Label);
        this.txt_info4 = this.FindTransform("txt_info4").getChildByName("text").getComponent(cc.Label);
        this.txt_info5 = this.FindTransform("txt_info5").getChildByName("text").getComponent(cc.Label);
        this.txt_info6 = this.FindTransform("txt_info6").getChildByName("text").getComponent(cc.Label);
        this.txt_info7 = this.FindTransform("txt_info7").getChildByName("text").getComponent(cc.Label);


        Util.SetClickAction(this.FindTransform("btnLeft").getComponent(cc.Button), () => {
            this.onClickLeft();
        })
        Util.SetClickAction(this.FindTransform("btnRight").getComponent(cc.Button), () => {
            this.onClickRight();
        })

        this.AdaptPhone();
    }

    private AdaptPhone()
    {
        // iPhone刘海适配
        if (cc.sys.os == cc.sys.OS_IOS)
        {
            let screen = cc.view.getFrameSize();
            let screenRate = screen.height / screen.width;
            if (screenRate >= 1.99)
            {
                this.ContentPanel.top = 50;
            }
        }
    }
     //添加复制按钮
     public addBtnCopyClick() {
        this.SetOnClick("btncopy1", () => {
            ClipboardJS.Copy(this.txt_info1.string);
            Toast.Show("复制成功");
        })
        this.SetOnClick("btncopy2", () => {
            ClipboardJS.Copy(this.txt_info2.string);
            Toast.Show("复制成功");
        })
        this.SetOnClick("btncopy3", () => {
            ClipboardJS.Copy(this.txt_info3.string);
            Toast.Show("复制成功");
        })
        this.SetOnClick("btncopy4", () => {
            ClipboardJS.Copy(this.txt_info4.string);
            Toast.Show("复制成功");
        })
        this.SetOnClick("btncopy5", () => {
            ClipboardJS.Copy(this.txt_info5.string);
            Toast.Show("复制成功");
        })
        this.SetOnClick("btncopy6", () => {
            ClipboardJS.Copy(this.txt_info6.string);
            Toast.Show("复制成功");
        })
        this.SetOnClick("btncopy7", () => {
            ClipboardJS.Copy(this.txt_info7.string);
            Toast.Show("复制成功");
        })


    }
    //充值金额获取
    public GetEditBoxStr(): string {
        return this.editbox_imput.string;
    }
    public ShowComByType(type: number) {
        switch (type) {
            case 1:
                this.RechargeCom.active = true;
                this.RechargeInfoCom.active = false;
                this.RechargeHistoryCom.active = false;
                break;
            case 2:
                this.RechargeCom.active = false;
                this.RechargeInfoCom.active = true;
                this.RechargeHistoryCom.active = false;
                this.BtnUpload.active = true;
                break;
            case 3:
                this.RechargeCom.active = false;
                this.RechargeInfoCom.active = false;
                this.RechargeHistoryCom.active = true;
                break;
        }

    }
    //显示图片
    public ShowImageUpload(data: string) {
        let ImageTest = this.FindTransform("ImgTest").getComponent(cc.Sprite);
        UploadIMG.showImage(data, ImageTest);
        this.BtnUpload.active = false;
    }
    public UpdataPage(data: any) {
        //更新银行列表
        this.BankItems = [];
        for (let i = 0; i < 6; i++) {
            let bankitem: cc.Node = cc.instantiate(this.bankItem);

            let _BankItem: BanKItem= new BanKItem();
            _BankItem.Init(bankitem, i, (banktype: number) => {
                cc.log("bnakType" + banktype);
            })
            bankitem.parent = this.BankList;
            this.BankItems.push(_BankItem);
        }
        //更新Bonus列表
        this.BonusItems = [];
        for (let i = 0; i < 6; i++) {
            let bonusitem: cc.Node = cc.instantiate(this.BonusItem);

            let _BonusItem: BounsItem = new BounsItem();
            _BonusItem.Init(bonusitem, i, (bounsNum: number) => {
                cc.log("bounsNum" + bounsNum);
            })
            bonusitem.parent = this.BonusList;
            this.BonusItems.push(_BonusItem);
        }
    }
    public InitDataPicker() {
        this.date = this.date ? this.date : new Date();
        this.year = this.date.getFullYear();
        this.month = this.date.getMonth();
        this.day = this.date.getDate();
        this.pfgListDay = [];
        for (let i = 0; i < 31; ++i) {
            let node = cc.instantiate(this.pfbDay);
            node.parent = this.ndDays;
            this.pfgListDay.push(node);
        }
        this.setDate(this.year, this.month, this.day)
    }
    private setDate(year, month, day) {
        this.date = new Date(year, month, day);
        this.year = this.date.getFullYear();
        this.month = this.date.getMonth();
        this.day = this.date.getDate();
        let wth = this.FindTransform("ndWeek").width / 7;
        let labelWth = this.FindTransform("lbDay0").width;
        for (let i = 0; i < 7; i++) {
            let daylabel = this.FindTransform("lbDay" + i);
            daylabel.x = i * wth + 50 - 7 * wth / 2;
        }

        this.updateDate();
    }
    private onClickLeft() {
        if (this.month > 0) {
            this.month -= 1;
        } else {
            this.month = 11;
            this.year -= 1;
        }
        this.date.setFullYear(this.year);
        this.date.setMonth(this.month);
        this.updateDate();
    }

    private onClickRight() {
        if (this.month < 11) {
            this.month += 1;
        } else {
            this.month = 0;
            this.year += 1;
        }
        this.date.setFullYear(this.year);
        this.date.setMonth(this.month);
        this.updateDate();
    }
    private updateDate() {
        this.lbYearMonth.string = cc.js.formatStr("%s年%s月", this.year, this.month + 1);
        cc.log(cc.js.formatStr("%s年%s月", this.year, this.month + 1))
        let date = new Date(this.year, this.month, 0);
        let totalDays = date.getDate();
        let fromWeek = date.getDay();
        for (let i = 0; i < this.pfgListDay.length; ++i) {
            let node = this.pfgListDay[i];
            if (i < totalDays) {
                node.width = this.ndDays.width / 7;
                node.active = true;
                let index = fromWeek + i;
                let row = Math.floor(index / 7);
                let col = index % 7;
                let x = -(this.ndDays.width - node.width) * 0.5 + col * node.width;
                let y = (this.ndDays.height - node.height) * 0.5 - row * node.height;
                node.setPosition(x, y);
                let dayScript: UIItemDay = new UIItemDay();

                dayScript.Init(node, i, i + 1, this.day === i + 1, (selIndex, selDay) => {
                    this.day = selDay;
                    cc.log("selDay", selDay);
                    this.updateDate();
                });
            } else {
                node.active = false;
            }
        }
    }



    public OnShowView()
    {

    }

    public OnHideView()
    {
        
    }

    public OnDestroy()
    {
        
    }
}
